<template>
  <div style="display: flex;overflow: auto">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;overflow: auto">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/realData" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">实时数据表</span>
        </el-menu-item>
        <el-menu-item index="/NumericValue" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">数值表表</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="padding: 20px 0 0 20px;width: 100%">
      <div style="padding-bottom: 20px">
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
<!--        <el-button type="success" @click="compareAndShowResult">对比判断</el-button> &lt;!&ndash; 新增按钮 &ndash;&gt;-->
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="ID" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="NFC标签值" prop="nfcNum"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="任务包编号" prop="packageNum"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="任务编号" prop="taskNum"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="录入形式" prop="entry"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备状态" prop="status"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="设备名称" prop="equipmentName"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="子设备名称" prop="childEquipment"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="参数名称" prop="parameter"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="参数类型" prop="type"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="数值型数据" prop="realData1" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="判断型数据" prop="realData2" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="判断结果" prop="result" align="center" :show-overflow-tooltip="true"></el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

    </div>
  </div>


</template>

<script>
export default {
  name: "RealData",
  data() {
    return {
      nfcNum:'',
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      department: '',
      packageNum: '',
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      ids: []
    }
  },
  created() {
    this.load()
  },
  methods: {
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/realData/export?token=' + this.user.token + "&packageNum=" + this.packageNum)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/realData/export?token=' + this.user.token + '&ids=' + idStr)

      }

    },
    compareAndShowResult() { // 新增方法，比较并显示结果
      this.$request.get('/realData/compare').then(res => {
        this.tableData = res.data
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/realData/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          packageNum: this.packageNum,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>
